body {
  background-color: #f5f5f5;
}

.demo-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  display: flex;
}

.demo-header {
  text-align: center;
  font-family: Courier New, Courier, monospace;
}

.user-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 100%;
  margin: 0 auto;
  font-family: Courier New, Courier, monospace;
  display: flex;
}

.name-change-header {
  margin-top: 2vh;
  margin-bottom: .5vh;
}

#single-user-demo {
  background-color: #fff;
  border: 1px solid #000;
  padding: 2%;
  font-size: 1.5em;
  transition: box-shadow .3s, transform .3s;
  box-shadow: 5px 5px #87878780;
}

#single-user-demo:hover {
  transform: scale(1.0175);
  box-shadow: 5px 5px #6d006db3;
}

.name-input {
  border-radius: none;
  border: 1px solid #000;
  width: 94%;
  height: 3.5vh;
  padding-left: 5px;
  font-family: Courier New, Courier, monospace;
  font-size: 1rem;
  transition: box-shadow .3s;
  box-shadow: 5px 5px #87878780;
}

.name-input:focus {
  border-radius: 0;
  outline: 0;
  box-shadow: 5px 5px #6d006db3;
}

.button-div {
  flex-flow: wrap;
  justify-content: start;
  align-items: center;
  margin-top: 3vh;
  display: flex;
}

.button-div > button {
  margin-right: 20px;
}

.form-header {
  margin-top: 0;
  margin-bottom: 10px;
}

#user-list {
  flex-flow: wrap;
  display: flex;
}

@media screen and (width <= 768px) {
  .demo-container {
    width: 90%;
  }

  .name-input {
    width: 90%;
    height: 4vh;
  }

  #single-user-demo {
    padding: 5%;
  }

  .button-div {
    margin-top: 3vh;
  }

  .button-div > button {
    width: 100%;
    margin-bottom: 1vh;
  }

  .name-change-header {
    margin-bottom: 2vh;
  }
}
